import React from 'react';
import FadeIn from 'react-fade-in/lib/FadeIn';

const WorkExperience = ({ experience, index }) => {
  console.log("experience")
  console.log(index)
  return (
    <div            data-aos="fade-up"
    data-aos-once className="work-experience" style={{flexDirection: index % 2 !== 0 ? 'row-reverse': 'row', }}>
      <div className="experience-info" style={{alignItems: index % 2 !== 0 ? 'flex-start' : 'flex-end', }}>


        <p className="experience-year">{experience.year} - </p>
        <h2 className="experience-title">{experience.title} <span className="experience-company">  {experience.company}</span></h2>


        <p className="experience-location">{experience.location}</p>
        {experience.image && (
          <img
            className="experience-image"
            src={experience.image}
            alt={`${experience.company} logo`}
            style={{ maxWidth: '100%', height: 'auto',}}
          />
        )}
              </div>
        <p className="experience-description">{experience.description}</p>
    </div>
  );
};

const WorkExperienceList = ({ experiences }) => {
  return (
    <div >
      {experiences.map((experience, index) => (
 
        <WorkExperience key={index} experience={experience} index={index}  />
      ))}
    </div>
  );
};

export default WorkExperienceList;

